@tailwind base;
@tailwind components;
@tailwind utilities;


.countdown-container {
  display: flex;
  flex-wrap: wrap; /* Added flex-wrap property */
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.countdown-item {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 10px 20px;
  border-radius: 8px;
  display: flex-col;
  color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.countdown-time {
  font-size: 2rem;
  font-weight: bold;
}

.countdown-label {
  font-size: 1rem;
  text-transform: uppercase;
}

.text-stroke-white {
  -webkit-text-stroke: .4px white;
  text-stroke: 1px white;
}
